<template>
  <div class="sort">
    <van-search placeholder="点击前往搜索" shape="round" />

    <!-- <van-tree-select
      height="85vh"
      :items="items"
      :main-active-index.sync="active"
    >
      <template #content>
        <div v-if="active === 0">
          <van-grid :column-num="3" :border="false">
            <van-grid-item v-for="item in classifyContent" :key="item.id">
              <van-image :src="item.pic" class="contentImg" lazy-load />
              <div style="font-family: Microsoft YaHei">{{ item.name }}</div>
            </van-grid-item>
          </van-grid>
        </div>
      </template>
    </van-tree-select> -->

    <!-- 左边 -->
    <van-sidebar v-model="activeKey" class="left" @change="sidebarChange">
      <van-sidebar-item
        v-for="item in items"
        :key="item.id"
        :title="item.text"
        style="text-align: center"
      />
    </van-sidebar>
    <!-- 右边 -->
    <div class="right">
      <div class="topContent">
        <van-image :src="top.pic" width="250px" />
      </div>
      <van-grid :column-num="3" :border="false">
        <van-grid-item v-for="item in classifyContent" :key="item.id">
          <van-image :src="item.pic" class="contentImg" lazy-load />
          <div style="font-family: Microsoft YaHei">{{ item.name }}</div>
        </van-grid-item>
      </van-grid>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeKey: 0,
      items: [],
      classifyContent: [],
      top: {},
    };
  },
  methods: {
    async getinfo() {
      const { data: res } = await this.$http.get("/sort");
      this.items = res.items;
      this.classifyContent = res.classifyContent;
      this.top = res.top;
    },
    async sidebarChange(e) {
      console.log(e);
      console.log(this.items[e].id);
      // const { data: res } = await this.$http.get("/sort/Inquire");
    },
  },
  created() {
    this.getinfo();
  },
};
</script>
<style lang="less" scoped>
.sort {
  margin-bottom: 50px;
}
.left {
  float: left;
  overflow: scroll;
  height: 88vh;
}
.right {
  margin-left: 80px;
  overflow: scroll;
  height: 84vh;
}
.topContent {
  text-align: center;
}
</style>